/* INFO PAGE STYLES*/
.info {
    background-color: #D43933;
    width: 100%;
    height: 100vh;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;

}

.infoContent {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 2.667rem;
}

.verticalPhoneImage {
    width: 19.900rem;
}

.infoTextContent {
    margin-left: 4.444rem;
  
}

.infoTextHeader {
    margin-bottom: 1.333rem;
    font-size: 1.8rem;
}

/* MEDIA QUERIES */
/* Adjustments for desktop devices */

@media (min-width: 1024px) {

    .verticalPhoneImage {
        width: 19.900rem;

    }

    .infoTextHeader {
        font-size: 1.7rem;

    }
}

/* Adjustments for tablet devices */
@media (min-width: 768px) and (max-width: 1023px) {

    .infoContent {
        flex-direction: row;
        justify-content: center;
        margin-left: 1rem;
        
       
    }

    .verticalPhoneImage {
        width: 16rem;

    }

    .infoTextHeader {
        font-size: 1.8rem;

    }
    .infoTextContent{
        font-size: 1.5rem;
    }
}



/* Adjustments for mobile devices */
@media (max-width: 767px) {

    .infoContent {
        flex-direction: column;
        align-items: center;
        font-size: 1rem;
        margin-bottom: 1rem;

    }

    .verticalPhoneImage {
        width: 16rem;
        margin-right: 0;
    }

    .infoTextContent {
        margin-left: 0;
        text-align: center;
        padding-left: 0.8rem;
        padding-right: 0.8rem;
        
       
    }

    .infoTextHeader {
        font-size: 1.3rem;
    }
}




/* Additional media query for smaller devices */
@media (max-width: 376px) {
    .verticalPhoneImage {
        width: 12rem;

    }
   
}