/* ABOUT PAGE STYLES */
.about {
  background-color: #D43933;
  width: 100%;
  height: 100vh;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.aboutContent {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 2.667rem;
}

.aboutTextContent {
  margin-left: 2.667rem;
}

.aboutTextHeader {
  margin-bottom: 1rem;
  font-size: 1.7rem;
}

/* MEDIA QUERIES  About */
/* Adjustments for larger laptops and desktops */
@media (min-width: 1024px) {
  .aboutContent {
    margin-top: 3.333rem;
  }
  .aboutTextContent {
    margin-left: 4.444rem;
  }
  .aboutTextHeader {
    font-size: 2rem;
  }
}

/* Adjustments for tablets and small laptops */
@media (min-width: 768px) and (max-width: 1023px) {
  .aboutContent {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
  .aboutTextContent {
    margin-left: 0;
    text-align: center;
  }
  .aboutTextHeader {
    font-size: 1.8rem;
  }
  .aboutTextParagraph {
    font-size: 1.5rem;
  }
}

@media (max-width: 767px) {
  /* Adjustments for mobile devices */
  .aboutContent {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
  .aboutTextContent {
    margin-left: 0;
    text-align: center;
    padding-left: 1.2rem;
    padding-right: 1.2rem;
  }
  .aboutTextHeader {
    font-size: 1.3rem;
  }
}
