/* CONTACT PAGE STYLES */
.contact {
    width: 100%;
    height: 100vh;
    background-color: #525e75c6;
    display: flex;
}

.contactContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 4.375rem;
}

.horizontalPhoneImage {
    width: 50.875rem;
    height: 24.875rem;
}

.contactTextContent {
    text-align: center;
    margin-top: 2.5rem;
}

.contactTextHeader {
    font-size: 1.7rem;
}

.contactTextParagraph {
    margin-top: 0.625rem;
}

/* MEDIA QUERIES Contact */

/* Adjustments for desktop devices */
@media (min-width: 1024px) {

    .horizontalPhoneImage {
        width: 50.875rem;
        height: 21.875rem;

    }

    .contactTextHeader {
        font-size: 1.7rem;

    }
}

/* Adjustments for tablet devices */
@media (min-width: 768px) and (max-width: 1023px) {

    .horizontalPhoneImage {
        width: 37rem;
        height: 18.5rem;

    }


    .contactTextContent {
        font-size: 1.5rem;
    }

    .contactTextHeader {
        font-size: 1.7rem;

    }
}



/* Adjustments for mobile devices */
@media (max-width: 767px) {

    .horizontalPhoneImage {
        max-width: 100%;
        max-height: 12.5rem;

    }

    .contactTextContent {
        padding-left: 1.2rem;
        padding-right: 1.2rem;
    }

    .contactTextHeader {
        font-size: 1.3rem;
    }
}