/* HOME PAGE STYLES */

.home {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background-color: #001233;
}

.homeContent {
    text-align: center;
    margin-bottom: 5rem;
    position: relative; 
    z-index: 1; 
}

.homeTextHeader {
    font-size: 2rem;
    margin-bottom: 1rem;
}

.homeTextParagraph {
    font-size: 1.2rem;
}

.logo {
    background-image: url('C:/Users/HDcomputers/Desktop/Belote-Club/belote-club/src/components/assets/logo.svg');
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 15vw;
    width: 10vw;
    height: 10vw;
    z-index: 0;
}

.linksSection {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.telegram {
    background-image: url('C:/Users/HDcomputers/Desktop/Belote-Club/belote-club/src/components/assets/telegram.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 21rem;
    height: 6em;
    margin-top: 50px;
    border-radius: 1.3%;
    margin-right: 3rem;
}

.socialMedia {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 40px;
}

.appStore {
    background-image: url('C:/Users/HDcomputers/Desktop/Belote-Club/belote-club/src/components/assets/Appstore.png');
    background-repeat: no-repeat;
    width: 180px;
    height: 50px;
    margin-right: 20px;
    background-size: contain;
    border-radius: 5%;
}

.playMarket {
    background-image: url('C:/Users/HDcomputers/Desktop/Belote-Club/belote-club/src/components/assets/Playmarket.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 180px;
    height: 50px;
    
}

/* MEDIA QUERIES */

/* Media Query for larger screens */


@media (min-width: 1200px) {
    .logo {
        left: 60px;
        width: 150px;
        height: 150px;
    }
}

/* Media Query for tablets and small laptops */
@media (min-width: 768px) and (max-width: 1199px) {
    .logo {
        left: 15vw;
        width: 17vw;
        height: 17vw;
    }

    .homeTextHeader {
        font-size: 1.5rem;
    }

    .homeTextParagraph {
        font-size: 1rem;
    }

    .telegram {
        width: 18rem;
        height: 5em;
    }

    .appStore,
    .playMarket {
        width: 140px;
        height: 40px;
    }
}

/* Media Query for mobile devices */
@media (max-width: 767px) {
    .logo {
        left: 10vw;
        width: 25vw;
        height: 25vw;
    }

    .homeTextHeader {
        font-size: 1.2rem;
        padding-left: 1.2rem;
        padding-right: 1.2rem;
    }

    .homeTextParagraph {
        font-size: 1rem;
    }

    .homeContent {
        margin-bottom: 3rem;
    }

    .telegram {
        width: 14rem;
        height: 4em;
        margin-right: 2rem;
    }

    .appStore,
    .playMarket {
        width: 120px;
        height: 35px;
    }
}